<template>
  <div class="liquidity-checker">
    <assets
      v-if="Object.keys(assets).length"
      :data="assets"
      :active-asset-id="activeAssetId"
      @update-active-asset="updateActiveAsset"
    />
    <asset
      v-if="activeAsset && socket && assetExchanges.length"
      :asset="activeAsset"
      :asset-exchanges="assetExchanges"
      :tabs="tabs"
      :active-tab="comparisonTab.newTab"
      :exchanges="exchanges"
      :is-loading="isAssetLoading"
      :last-updated-asset="lastUpdatedAsset"
      @update-comparison-tab="updateComparisonTab"
    />
    <loader :is-active="isLoaderActive" />
  </div>
</template>

<script>
import api from '@/api';
import Asset from '@/components/Asset/Index';
import Assets from '@/components/Assets';
import Loader from '@/components/Loader';

const MSG_TYPE = {
  MESSAGE: 'message'
};
const MSG_ASSET = {
  GROUPS: 'PC'
};

const EXCHANGES = {};
// const MESSAGE_TYPES = {
//   // AP: 'updateAssetsPricesMsg'
// };

// const SUBSCRIBE_AP_MSG = '["SUBSCRIBE", "AP"]';

export default {
  name: 'LiquidityChecker',
  components: { Assets, Asset, Loader },
  data() {
    return {
      socket: null,
      isSocketConnected: false,
      isWebsocketClosedNormaly: false,
      assets: {},
      isAssetLoading: true,
      activeAssetId: null,
      tabs: [],
      assetExchanges: [],
      isLoaderActive: false,
      comparisonTab: { oldTab: '', newTab: '' },
      lastUpdatedAsset: Date.now()
    };
  },

  computed: {
    activeAsset() {
      return this.assets[this.activeAssetId] || null;
    },

    exchanges() {
      return EXCHANGES;
    }
  },

  created() {
    this.init();
  },

  beforeDestroy() {
    this.isWebsocketClosedNormaly = true;
    this.socket.close();
  },

  methods: {
    async init() {
      try {
        this.isLoaderActive = true;
        await Promise.all([this.fetchExchanges(), this.fetchAllAssets()]);
        this.newWebsocketConnection();
      } catch (error) {
        console.error(error);
      } finally {
        this.isLoaderActive = false;
      }
    },

    newWebsocketConnection() {
      this.socket = new WebSocket(process.env.VUE_APP_HOST_WS);
      this.socket.onopen = this.onOpenWebSocket;
      this.socket.onmessage = this.newWebsocketMsg;
      this.socket.onclose = (e) => {
        this.isSocketConnected = false;
        if (this.isWebsocketClosedNormaly) return;
        console.log('Socket is closed. Reconnect will be attempted in 1 second.', e.reason);
        setTimeout(this.newWebsocketConnection, 1000);
      };
      this.socket.onerror = (err) => {
        console.error('Socket encountered error: ', err.message, 'Closing socket');
        this.socket.close();
        this.isSocketConnected = false;
      };
    },

    onOpenWebSocket() {
      this.isSocketConnected = true;
      if (this.comparisonTab.newTab) {
        this.updateComparisonTab(this.comparisonTab);
      }
    },

    newWebsocketMsg({ data }) {
      const parsedData = JSON.parse(data);
      console.log('newWebsocketMsg -> parsedData', parsedData);
      const typeOfMsg = parsedData.SUBSCRIBE[0];
      // if not 'message' type of message
      if (typeOfMsg !== MSG_TYPE.MESSAGE) return;
      const [msgGroup, asset, tab] = parsedData.SUBSCRIBE[1].split(',');
      // need only for group msgs
      if (msgGroup !== MSG_ASSET.GROUPS) return;
      // check is same as active asset
      if (asset !== this.activeAssetId) return;
      // check is same as active tab
      if (tab !== this.comparisonTab.newTab) return;
      const { msgBody } = JSON.parse(`{ "msgBody": ${parsedData.SUBSCRIBE[2]} }`);
      const msgBodyAsObj = msgBody.reduce((res, [symbolid, price, volume]) => {
        res[symbolid] = { price, volume };
        return res;
      }, {});
      this.assetExchanges.forEach((asset) => {
        if (Object.prototype.hasOwnProperty.call(msgBodyAsObj, asset.symbolid)) {
          asset.price = msgBodyAsObj[asset.symbolid].price;
          asset.volume = msgBodyAsObj[asset.symbolid].volume;
          asset.updatedAt = Date.now();
        }
      });
    },

    async fetchAllAssets() {
      try {
        const assets = await api.assets.getAll();
        this.assets = assets.data.reduce((res, el) => {
          res[el.id] = el;
          return res;
        }, {});
      } catch (error) {
        console.error(error);
      }
    },

    async fetchExchanges() {
      try {
        const exchanges = await api.exchanges.getAll();
        exchanges.data.forEach((el) => (EXCHANGES[el.id] = el));
      } catch (error) {
        console.error(error);
      }
    },

    updateAssetsPricesMsg(msg) {
      const parsedMsgData = JSON.parse(`{ "data": ${msg} }`);
      if (!Array.isArray(parsedMsgData.data)) return;
      parsedMsgData.data.forEach((el) => {
        const asset = this.assets[el[0]];
        if (!asset) return;
        asset.price = el[1];
      });
    },

    async updateActiveAsset(assetId) {
      if (this.activeAssetId === assetId) {
        this.lastUpdatedAsset = Date.now();
        return;
      }
      this.isAssetLoading = true;
      try {
        this.lastUpdatedAsset = Date.now();
        this.activeAssetId = assetId;
        await this.fetchAssetTabs();
        this.comparisonTab.oldTab = this.comparisonTab.newTab;
        this.comparisonTab.newTab = this.tabs[0];
        await this.updateComparisonTab(this.comparisonTab);
      } catch (error) {
        console.error(error);
      } finally {
        this.isAssetLoading = false;
      }
    },

    async updateComparisonTab({ oldTab, newTab }) {
      this.isAssetLoading = true;
      try {
        await this.fetchAssetExchanges(newTab);
        this.comparisonTab.oldTab = oldTab;
        this.comparisonTab.newTab = newTab;
        if (!this.isSocketConnected) return;
        if (oldTab) {
          this.socket.send(`["UNSUBSCRIBE","PC,${this.activeAssetId},${oldTab}"]`);
        }
        return this.socket.send(`["SUBSCRIBE","PC,${this.activeAssetId},${newTab}"]`);
      } catch (error) {
        console.error(error);
      } finally {
        this.isAssetLoading = false;
      }
    },

    async fetchAssetExchanges(tab) {
      try {
        const assetExchanges = await api.assets.getGroupsByAssetId(this.activeAssetId, tab);
        this.assetExchanges = assetExchanges.data;
      } catch (error) {
        console.error(error);
      }
    },

    async fetchAssetTabs() {
      try {
        const tabs = await api.assets.getGroupsByAssetId(this.activeAssetId);
        this.tabs = tabs.data;
      } catch (error) {
        console.error(error);
      }
    }
  }
};
</script>

<style lang="sass" scoped>
.liquidity-checker
  position: relative
  display: flex
  max-width: 100%
  width: 100%
  max-height: 650px
  height: calc(100vh - 60px)
  overflow: hidden
</style>
