<template>
  <table class="exchanges-table">
    <caption class="exchanges-table__caption">
      <button
        class="exchanges-table__caption-btn"
        :class="isClosed ? 'exchanges-table__caption-btn--closed' : ''"
        @click="$emit('on-back-click')"
      >
        <img
          :src="require('@/assets/img/arrow.svg')"
          width="18"
          alt="BACK"
        >
      </button>
      <asset-icon
        :id="asset.id"
        :key="asset.id"
        :symbol="asset.symbol"
        class="exchanges-table__caption-icon"
      />
      <span class="exchanges-table__caption-text">{{ $t('compare') }} {{ asset.title }} </span>
      {{ `${asset.symbol}/${activeTab}` }}
    </caption>
    <thead>
      <th>{{ $t('exchange') }}</th>
      <th> {{ $t('tradingPair') }}</th>
      <th class="exchanges-table__volume-th">
        {{ $t('24h') }} <span>{{ $t('volume') }}</span> ({{ asset.symbol }})
      </th>
      <th>{{ $t('lastPrice') }}</th>
      <th class="exchanges-table__updated-th">{{ $t('updated') }}</th>
    </thead>
    <tbody>
      <tr
        v-for="exchange in data"
        :key="exchange.symbolid"
      >
        <td class="exchanges-table__title">
          <img
            :src="require(`@/assets/img/ex/${exchange.exchange.toLowerCase()}.svg`)"
            :alt="exchange.id"
          >
          {{ exchanges[exchange.exchange].title }}
        </td>
        <td class="exchanges-table__pair">{{ exchange.ticker }}
          <span
            v-if="exchange.type !== 'spot'"
            class="exchanges-table__type"
          >{{ EXCHANGES_TYPES[exchange.type] }}</span>
        </td>
        <td class="exchanges-table__volume">{{ exchange.volume | numAbbr }}</td>
        <td class="exchanges-table__price">{{ exchange.price }}</td>
        <td class="exchanges-table__updated">
          {{ exchange.updatedAt ? calculateFromNowTime(exchange.updatedAt) : '' }}
        </td>
        <td class="exchanges-table__link">
          <a
            :href="`https://app.goodcrypto.app/trade/${exchange.exchange}/${exchange.ticker}`"
            target="_blank"
          >
            <img
              :src="require('@/assets/img/trade2.svg')"
              alt="Trade"
            ></a>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
const EXCHANGES_TYPES = {
  future: 'Futures',
  swap: 'Swap'
};

import AssetIcon from '@/components/Asset/Icon';
const abbreviate = require('number-abbreviate');
import dayjs from 'dayjs';
const relativeTime = require('dayjs/plugin/relativeTime');
dayjs.extend(relativeTime);

export default {
  name: 'ExchangesTable',
  components: { AssetIcon },
  filters: {
    numAbbr(v) {
      if (v > 9999) return abbreviate(v, 2).toUpperCase();
      return v;
    }
  },
  props: {
    data: { type: Array, required: true },
    exchanges: { type: Object, required: true },
    asset: { type: Object, required: true },
    activeTab: { type: String, required: true },
    isClosed: { type: Boolean, required: true }
  },
  data() {
    return {};
  },

  computed: {
    EXCHANGES_TYPES() {
      return EXCHANGES_TYPES;
    }
  },

  created() {
    this.init();
  },

  methods: {
    init() {},

    calculateFromNowTime(time) {
      return `${dayjs(time).fromNow(true)} ago`;
    }
  }
};
</script>

