<template>
  <div class="asset-icon">
    <img
      v-if="iconUrl"
      :src="iconUrl"
      :alt="`icon ${symbol}`"
      class="asset-icon__img"
    >
    <span
      v-else
      class="asset-icon__img-placeholder"
      :style="{ 
        fontSize: calcFontSize(symbol.substr(0, 5)),
        backgroundColor: getBgColor(symbol)
      }"
    >{{ symbol.substr(0, 5) }}</span>
  </div>
</template>

<script>
export default {
  name: 'AssetIcon',
  components: {},
  props: {
    id: {
      type: String,
      required: true
    },
    symbol: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      iconUrl: null
    };
  },

  created() {
    this.init();
  },

  methods: {
    init() {
      this.checkIconUrl();
    },

    checkIconUrl() {
      try {
        this.iconUrl = require(`@/assets/img/coin-icons/${this.id}.svg`);
      } catch (error) {
        this.iconUrl = null;
      }
    },

    calcFontSize(symbol) {
      if (symbol.length < 2) return '14px';
      else if (symbol.length < 3) return '12px';
      else if (symbol.length < 4) return '11px';
      else return '8px';
    },

    getBgColor(symbol) {
      return [0, 1, 2].reduce((res, el) => {
        const code = symbol.charCodeAt(el) || 64;
        return (res += ('0' + (code - 64) * 4).slice(-2));
      }, '#');
    }
  }
};
</script>

<style lang="sass" scoped>
