<template>
  <div
    id="liquidity-checker-app"
    :class="scrollbarStyled ? 'scrollbar__styled' : ''"
  >
    <liquidity-checker />
  </div>
</template>

<script>
import LiquidityChecker from '@/components/LiquidityChecker';

export default {
  name: 'App',
  components: { LiquidityChecker },
  data() {
    return {
      scrollbarStyled: false
    };
  },
  created() {
    // Detecting windows and add new class for styling scroll
    const isWindows =
      ['Win32', 'Win64', 'Windows', 'WinCE'].indexOf(window.navigator.platform) !== -1;
    if (isWindows) this.scrollbarStyled = true;
  }
};
</script>

