<template>
  <div class="assets">
    <div class="assets__wrap">
      <label class="assets__search-wrap">
        <input
          v-model.trim="search"
          :placeholder="$t('searchCoins')"
          type="text"
          class="assets__search-input"
        >
      </label>
      <ul
        v-if="preparedAssets.length"
        class="assets__list"
        @scroll="onScrollAssets"
      >
        <li
          v-for="asset in preparedAssets"
          :key="asset.id"
          class="assets__el"
          :class="activeAssetId === asset.id ? 'assets__el--active' :''"
        >
          <button @click="$emit('update-active-asset', asset.id)">
            <asset-icon
              :id="asset.id"
              :symbol="asset.symbol"
              class="assets__el-inner"
            />
            <div class="assets__el-inner assets__el-name">{{ asset.title }}</div>
            <span class="assets__el-symbol">{{ asset.symbol }}</span>
            <img
              :src="require('@/assets/img/arrow-angle.svg')"
              class="assets__el-arrow"
            >
          </button>
        </li>
      </ul>
      <p
        v-else
        class="assets__list-empty-msg"
      >{{ $t('notFound') }}</p>
    </div>
  </div>
</template>

<script>
import AssetIcon from '@/components/Asset/Icon.vue';
import { throttle } from 'lodash';

export default {
  name: 'Assets',
  components: { AssetIcon },
  props: {
    data: { type: Object, required: true },
    activeAssetId: { type: String, default: null }
  },
  data() {
    return {
      search: null,
      assetsPerPage: 20,
      assetsShowLimit: 20
    };
  },

  computed: {
    preparedAssets() {
      let assetsArray = Object.values(this.data);
      if (!assetsArray.length) return [];
      // Filter by serach
      if (this.search) {
        assetsArray = assetsArray.filter((asset) => {
          return (
            asset.title.toLowerCase().includes(this.search.toLowerCase()) ||
            asset.symbol.toLowerCase().includes(this.search.toLowerCase())
          );
        });
      }
      // Sort by Rank
      assetsArray = assetsArray.sort((a, b) => a.rank - b.rank);
      // Splice into pagination limit
      assetsArray = assetsArray.slice(0, this.assetsShowLimit);
      return assetsArray;
    }
  },

  created() {
    this.init();
  },

  methods: {
    init() {
      this.$emit('update-active-asset', this.preparedAssets[0].id);
      this.onScrollAssets = throttle(this.onScrollAssets, 500);
    },

    onScrollAssets(e) {
      const height = e.target.offsetHeight;
      const scrollHeight = e.target.scrollHeight;
      const scrollTop = e.target.scrollTop;
      if (height + scrollTop + 500 < scrollHeight) return;
      if (this.preparedAssets.length < this.assetsShowLimit) return;
      this.assetsShowLimit += this.assetsPerPage;
    }
  }
};
</script>

