<template>
  <div
    class="asset"
    :class="isClosed ? 'is-closed' : ''"
  >
    <div
      v-if="tabs.length"
      class="asset__tabs"
    >
      <comparison-tabs
        :tabs="tabs"
        :active-tab="activeTab"
        @update-active-tab="updateActiveTab"
      />
    </div>
    <div class="asset__tables">
      <div
        v-if="assetExchangesBase.length && activeTab"
        class="asset__table-wrap"
      >
        <exchanges-table
          :data="assetExchangesBase"
          :exchanges="exchanges"
          :asset="asset"
          :active-tab="activeTab"
          :is-closed="isClosed"
          @on-back-click="isClosed = !isClosed"
        />
      </div>
      <div
        v-if="assetExchangesNotBase.length && activeTab"
        class="asset__table-wrap"
      >
        <exchanges-table
          :data="assetExchangesNotBase"
          :exchanges="exchanges"
          :asset="asset"
          :active-tab="activeTab"
          :is-closed="isClosed"
          @on-back-click="isClosed = !isClosed"
        />
      </div>
    </div>
    <loader :is-active="isLoading" />
  </div>
</template>

<script>
import СomparisonTabs from '@/components/Asset/СomparisonTabs';
import ExchangesTable from '@/components/Asset/ExchangesTable';
import Loader from '@/components/Loader';

export default {
  name: 'Asset',
  components: { 'comparison-tabs': СomparisonTabs, ExchangesTable, Loader },
  props: {
    asset: { type: Object, required: true },
    assetExchanges: { type: Array, required: true },
    exchanges: { type: Object, required: true },
    tabs: { type: Array, required: true },
    activeTab: { type: String, required: true },
    isLoading: { type: Boolean, default: false },
    lastUpdatedAsset: { type: Number, required: true }
  },
  data() {
    return {
      isClosed: false
    };
  },

  computed: {
    assetExchangesBase() {
      return this.assetExchanges.filter((el) => el.isBase);
    },

    assetExchangesNotBase() {
      return this.assetExchanges.filter((el) => !el.isBase);
    }
  },

  watch: {
    // asset() {
    //   this.isClosed = true;
    // },

    lastUpdatedAsset() {
      this.isClosed = true;
    }
  },

  created() {
    this.init();
  },

  methods: {
    async init() {},

    updateActiveTab(newTab) {
      this.$emit('update-comparison-tab', { oldTab: this.activeTab, newTab });
    }
  }
};
</script>

