<template>
  <div class="comparison-tabs">
    <ul
      ref="tabsList"
      class="comparison-tabs__list"
    >
      <li
        v-for="tab in acceptedTabs"
        :key="tab"
        class="comparison-tabs__el"
      >
        <button
          :disabled="tab === activeTab"
          @click="$emit('update-active-tab', tab)"
        >{{ tab }}</button>
      </li>
      <li class="comparison-tabs__el comparison-tabs__el-add">
        <button
          v-if="!isNewTabSelectorVisible"
          @click="showTabSelector"
        >
          <img
            :src="require('@/assets/img/add-plus.svg')"
            width="20"
            height="20"
            alt="add tab"
          >
        </button>
      </li>
    </ul>
    <multiselect
      v-if="isNewTabSelectorVisible"
      :options="tabs.filter(el => !acceptedTabs.includes(el))"
      :show-labels="false"
      class="comparison-tabs__el-add-select"
      :placeholder="$t('selectCurrency')"
      @select="selectedNewTab"
    >

      <span slot="noResult">{{ $t('notFound') }}</span>
    </multiselect>
  </div>
</template>

<script>
import Multiselect from 'vue-multiselect';

const MAX_TABS = 3;

export default {
  name: 'ExchangesTabs',
  components: { Multiselect },
  props: {
    tabs: { type: Array, required: true },
    activeTab: { type: String, required: true }
  },
  data() {
    return {
      acceptedTabs: [],
      isNewTabSelectorVisible: false
    };
  },

  watch: {
    tabs: {
      immediate: true,
      handler() {
        this.acceptedTabs.splice(0);
        this.acceptedTabs.push(...this.tabs.slice(0, MAX_TABS));
      }
    }
  },

  created() {
    this.init();
  },

  methods: {
    init() {},

    async showTabSelector() {
      this.isNewTabSelectorVisible = true;
      await this.$nextTick();
      this.$refs.tabsList.scroll(9999999, 0);
    },

    selectedNewTab(tab) {
      this.acceptedTabs.push(tab);
      this.$emit('update-active-tab', tab);
      this.isNewTabSelectorVisible = false;
    }
  }
};
</script>

